
/* for the cart side panel*/
.cartPanelContainer {
    position: absolute;
    background-color: #ffffff;
    flex-direction: column;
}

.panel-container {
    height: 100%;
    width: 100%;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.sliding-panel-container {
    position: fixed;
    top: 0;
    left: 0;
}

    .sliding-panel-container.active {
        position: fixed;
        z-index: 15000;
        background-color: rgba(0, 0, 0, 0.3);
        will-change: transform;
    }

        .sliding-panel-container.active.click-through {
            pointer-events: none;
            background-color: transparent;
        }

    .sliding-panel-container.click-through > div {
        pointer-events: none;
    }

.panel-content {
    height: 100%;
    width: 100%;
    pointer-events: all;
}

.panel-container-left-enter {
    opacity: 0;
    transform: translateX(-100%);
}

.panel-container-left-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-left-exit {
    opacity: 1;
    transform: translateX(0);
}

.panel-container-left-exit-active {
    opacity: 0;
    transform: translateX(-100%);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-right-enter {
    opacity: 0;
    transform: translateX(100%);
}

.panel-container-right-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-right-exit {
    opacity: 1;
    transform: translateX(0);
}

.panel-container-right-exit-active {
    opacity: 0;
    transform: translateX(100%);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-top-enter {
    opacity: 0;
    transform: translateY(-100%);
}

.panel-container-top-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-top-exit {
    opacity: 1;
    transform: translateX(0);
}

.panel-container-top-exit-active {
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-bottom-enter {
    opacity: 0;
    transform: translateY(100%);
}

.panel-container-bottom-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: transform 500ms, opacity 500ms;
}

.panel-container-bottom-exit {
    opacity: 1;
    transform: translateX(0);
}

.panel-container-bottom-exit-active {
    opacity: 0;
    transform: translateY(100%);
    transition: transform 500ms, opacity 500ms;
}


/*rendering items on Cart Side Panel*/

#cartList {
margin-bottom: 50px;
}

#cartItemContainer {
    margin-top: 10px;
    margin-bottom: 10px;
    height: 200px;
    width: 100%;
}

#cartImageContainer {
    margin-right: 50px;
    display: inline-block;
}

.cartItemImage {
    width: auto;
    height: 100%;
    max-height: 150px;
    margin-left: 10px;
    margin-right: 10px;
}

.cartItemText {
    margin: 10px;
    text-align: left;
    display: inline-block;
}

@media screen and (max-width: 540px) {

    #cartItemContainer {
        height: 415px;
    }
}